import React from 'react';

const ScreenLoading = () => {
  return (
    <div
      className={
        'absolute flex top-0 left-0 w-[100vw] h-[100vh] bg-gray items-center justify-center z-20 backdrop-blur-[20px]'
      }
    >
      <img width={40} src="/images/MINTCHIP_Loading_Gif.gif" />
    </div>
  );
};

export default ScreenLoading;
