import React from 'react';
import type { NextPage } from 'next';

type TabType = {
  selectedTab: number;
  onChange: (value: number) => void;
};

const Tab: NextPage<TabType> = ({ selectedTab, onChange }) => {
  return (
    <div className="self-stretch flex flex-row items-start justify-start gap-[32px] text-xl text-grey-4001 border-b-[1px] border-solid border-alpha-30">
      <div
        className={
          'flex flex-col items-start justify-start gap-[12px] cursor-pointer border-solid' +
          (selectedTab === 0
            ? ' border-b-[1px] border-solid border-gray-2'
            : '')
        }
        onClick={() => onChange(0)}
      >
        <div
          className={
            'relative text-[16px] md:text-[18px] leading-[25px] md:leading-[28px] font-medium' +
            (selectedTab === 0 ? ' text-gray-2' : ' text-gray-1')
          }
        >
          Profile
        </div>
      </div>
      <div
        className={
          'flex flex-col items-start justify-start gap-[12px] cursor-pointer' +
          (selectedTab === 1
            ? ' border-b-[1px] border-solid border-gray-2'
            : '')
        }
        onClick={() => onChange(1)}
      >
        <div
          className={
            'relative text-[16px] md:text-[18px] leading-[25px] md:leading-[28px] font-medium' +
            (selectedTab === 1 ? ' text-gray-2' : ' text-gray-1')
          }
        >
          Security
        </div>
      </div>
    </div>
  );
};

export default Tab;
