import React, { useEffect, useRef, useState } from 'react';
import type { NextPage } from 'next';
import { ICON_NAMES, SVGIcon } from '@react-page/editor';
import { useRouter } from 'next/router';
import ScreenLoading from './screenLoading';

type navigationType = {
  page: string;
};

const Navigation: NextPage<navigationType> = ({ page }) => {
  const router = useRouter();
  const menuItems = [
    {
      key: 'home',
      name: 'My Sites',
      icon: ICON_NAMES.HOME,
      status: true,
      url: '/home',
    },
    {
      key: 'collectibles',
      name: 'Collectibles',
      icon: ICON_NAMES.COLLECTIBLES,
      status: true,
      url: '/collectibles',
    },
    {
      key: 'golive',
      name: 'Go live',
      icon: ICON_NAMES.PLAY,
      status: true,
      url: '/golive',
    },
    {
      key: 'settings',
      name: 'Settings',
      icon: ICON_NAMES.SETTING,
      status: true,
      url: '/settings',
    },
    {
      key: 'content',
      name: 'Exclusive Content',
      icon: ICON_NAMES.EXCLUSIVE,
      status: false,
      url: '/content',
    },
    {
      key: 'rewards',
      name: 'Rewards',
      icon: ICON_NAMES.REWARDS,
      status: false,
      url: '/rewards',
    },
    {
      key: 'fans',
      name: 'Your top fans',
      icon: ICON_NAMES.FANS,
      status: false,
      url: '/fans',
    },
  ];

  const wrapperRef = useRef<HTMLDivElement>(null);

  const [open, setOpen] = useState(false);
  const [modalId, setModalId] = useState('');
  const [loading, setLoading] = useState(false);

  const handleClick = (key: string, url: string, status: boolean) => {
    setModalId(key);
    if (status) {
      setLoading(true);
      router.push({
        pathname: url,
      });
      setLoading(false);
    } else {
      setOpen(true);
    }
  };

  useEffect(() => {
    const handleClickOutside = (event: MouseEvent) => {
      const { current: wrap } = wrapperRef!;
      if (wrap && !wrap.contains(event.target as Node)) {
        setOpen(false);
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [wrapperRef]);

  if (loading === true) {
    return <ScreenLoading />;
  }

  return (
    <div>
      {/* desktop navigation */}
      <div className="hidden md:flex flex-col w-[270px] bg-primary-mint h-[100vh] rounded-r-[48px] pt-16 px-[20px] pb-8 gap-[64px]">
        <div className="self-stretch flex flex-col py-0 px-5 items-center justify-start gap-[8px]">
          <div className="relative w-[183px] h-10 shrink-0 overflow-hidden hidden"></div>
          <img
            className="relative w-[158px] h-[29px] shrink-0 object-cover"
            alt=""
            src="/images/mintchip-beta-logo-1.png"
          />
        </div>

        <div className="self-stretch flex-1 flex flex-col items-start justify-start gap-[8px]">
          {menuItems.map((item, index) => (
            <div className="w-full" key={index}>
              <div
                key={index}
                className={
                  'cursor-pointer self-stretch rounded-xl h-12 shrink-0 flex flex-row py-2 px-5 box-border items-center justify-start gap-[12px] opacity-100 hover:opacity-60' +
                  (page === item.key ? ' bg-gray' : '')
                }
                onClick={() => handleClick(item.key, item.url, item.status)}
              >
                <SVGIcon
                  name={item.icon}
                  active={page === item.key}
                  black={page !== item.key && item.status}
                />
                <div
                  className={
                    'relative leading-[28px] font-medium' +
                    (page === item.key
                      ? ' text-primary-mint'
                      : item.status
                      ? ' text-gray'
                      : ' text-gray-1')
                  }
                >
                  {item.name}
                </div>
              </div>
              {!item.status && modalId === item.key && (
                <div className="absolute">
                  <div
                    ref={wrapperRef}
                    className={
                      'relative bg-gray-4 left-[230px] top-[-50px] w-[320px] z-20 rounded-[12px] p-[16px]' +
                      (open ? ' block' : ' hidden')
                    }
                  >
                    <div className="text-gray-2">Coming soon!</div>
                    <div className="text-gray-1">
                      We are working to give you all the fan engagement tools
                      your heart desires!
                      <br />
                      <br />
                      {`Stay tuned:)`}
                    </div>
                  </div>
                </div>
              )}
            </div>
          ))}
        </div>
      </div>

      {/* mobile navigation */}
      <div className="flex fixed bottom-0 md:hidden flex-row w-full bg-primary-mint h-[72px] justify-between rounded-t-[20px] px-[23px] py-[13px] z-20">
        {menuItems.map(
          (item, index) =>
            item.status && (
              <div
                key={index}
                className={
                  'cursor-pointer self-stretch rounded-xl h-12 shrink-0 flex flex-row py-2 px-5 box-border items-center justify-start gap-[12px] opacity-100 hover:opacity-60' +
                  (page === item.key ? ' bg-gray' : '')
                }
                onClick={() => handleClick(item.key, item.url, item.status)}
              >
                <SVGIcon
                  name={item.icon}
                  active={page === item.key}
                  black={page !== item.key && item.status}
                />
              </div>
            )
        )}
      </div>
    </div>
  );
};

export default Navigation;
