import React, { useState, useEffect } from 'react';
import type { NextPage } from 'next';
import { toast } from 'react-toastify';
import { ButtonLoading } from '@react-page/editor';
import { ActionType, useStateContext } from '../../../../state';
import Cookies from 'js-cookie';
import { useRouter } from 'next/router';

const SecurityTab: NextPage = () => {
  const {
    state: { user, token },
    dispatch,
  } = useStateContext();
  const [newPassword, setNewPassword] = useState('');
  const [currentPassword, setCurrentPassword] = useState('');
  // const [twoFactor, setTwoFactor] = useState(true);
  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);
  const [changepassLoading, setChangepassLoading] = useState(false);
  const [show, setShow] = useState(false);
  const [cshow, setCshow] = useState(false);

  const handleChangePassword = async () => {
    setChangepassLoading(true);
    const data = { currentPassword: currentPassword, newPassword: newPassword };
    const options = {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
      body: JSON.stringify(data),
    };

    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/settings/${Cookies.get(
          '_id'
        )}/change-password`,
        options
      )
        .then((res) => res.json())
        .then((res) => {
          if (res.code === 200) {
            setChangepassLoading(false);
            toast.success('Successfully changed!');
          } else {
            toast.error(res.message);
          }
          setChangepassLoading(false);
        });
    } catch (error) {
      toast.error('Network Error!');
      setChangepassLoading(false);
    }
  };

  // useEffect(() => {
  //   if (user) {
  //     setTwoFactor(user.twoFactorEnabled);
  //   }
  // }, [user]);

  // const handleChangeTwoFactor = async (value: boolean) => {
  //   setTwoFactor(value);
  // };

  const router = useRouter();

  const handleDeleteAccount = async () => {
    setLoading(true);
    const options = {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
    };

    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/settings/${Cookies.get(
          '_id'
        )}`,
        options
      )
        .then((res) => res.json())
        .then((res) => {
          if (res.code === 200) {
            setOpen(false);
            dispatch({
              type: ActionType.UPDATE_USER,
              payload: {
                _id: '',
                email: '',
                phone: '',
                firstname: '',
                lastname: '',
                image: '',
              },
            });
            dispatch({
              type: ActionType.UPDATE_TOKEN,
              payload: '',
            });
            router.push('/');
          } else {
            toast.error(res.message);
          }
          setOpen(false);
        });
    } catch (error) {
      toast.error('Network Error!');
      setOpen(false);
    }
    setOpen(false);
  };

  return (
    <div className="flex flex-col items-start justify-start gap-[40px] text-left text-xl text-primary-grey-501 font-text-xs-bold1">
      <b className="self-stretch relative text-4xl leading-[38px] text-gray-2">
        Security
      </b>
      <div className="self-stretch flex flex-col items-start justify-start gap-[16px]">
        <div className="self-stretch flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            Old password
          </div>
          <div className="self-stretch relative text-base leading-[24px] text-grey-4001 text-gray-1"></div>
        </div>
        <div className="flex relative self-stretch rounded-xl bg-light-101 h-12 shrink-0 flex-row py-2 box-border items-center justify-start text-lg">
          <input
            value={currentPassword}
            type={cshow ? 'text' : 'password'}
            onChange={(e) => setCurrentPassword(e.target.value)}
            placeholder="&#9679;&#9679;&#9679;&#9679;&#9679;"
            className="bg-alpha-10 tracking-[3px] text-[14px] focus:outline-none focus:border-white border-[1px] focus:border-solid rounded-[5px] w-full h-[48px] pl-[20px] pr-[45px] text-gray-2"
          />
          <div className="absolute h-full flex items-center right-[15px]">
            <button
              onClick={() => setCshow(!cshow)}
              className="flex items-center focus:outline-none bg-transparent w-[20px] h-[20px] p-0"
            >
              {cshow === true ? (
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="white"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                  ></path>
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
                  ></path>
                </svg>
              ) : (
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="gray"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21"
                  ></path>
                </svg>
              )}
            </button>
          </div>
        </div>
        <div className="self-stretch flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            New password
          </div>
        </div>
        <div className="flex relative self-stretch rounded-xl bg-light-101 h-12 shrink-0 flex-row py-2 box-border items-center justify-start text-lg">
          <input
            value={newPassword}
            type={show ? 'text' : 'password'}
            onChange={(e) => setNewPassword(e.target.value)}
            placeholder="&#9679;&#9679;&#9679;&#9679;&#9679;"
            className="bg-alpha-10 tracking-[3px] text-[14px] focus:outline-none focus:border-white border-[1px] focus:border-solid rounded-[5px] w-full h-[48px] pl-[20px] pr-[45px] text-gray-2"
          />
          <div className="absolute h-full flex items-center right-[15px]">
            <button
              onClick={() => setShow(!show)}
              className="flex items-center focus:outline-none bg-transparent w-[20px] h-[20px] p-0"
            >
              {show === true ? (
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="white"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M15 12a3 3 0 11-6 0 3 3 0 016 0z"
                  ></path>
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z"
                  ></path>
                </svg>
              ) : (
                <svg
                  className="w-6 h-6"
                  fill="none"
                  stroke="gray"
                  viewBox="0 0 24 24"
                  xmlns="http://www.w3.org/2000/svg"
                >
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth="2"
                    d="M13.875 18.825A10.05 10.05 0 0112 19c-4.478 0-8.268-2.943-9.543-7a9.97 9.97 0 011.563-3.029m5.858.908a3 3 0 114.243 4.243M9.878 9.878l4.242 4.242M9.88 9.88l-3.29-3.29m7.532 7.532l3.29 3.29M3 3l3.59 3.59m0 0A9.953 9.953 0 0112 5c4.478 0 8.268 2.943 9.543 7a10.025 10.025 0 01-4.132 5.411m0 0L21 21"
                  ></path>
                </svg>
              )}
            </button>
          </div>
        </div>
        <div className="rounded-xl bg-light-101 h-12 shrink-0 flex flex-row py-4 box-border items-center justify-center text-center text-base">
          <button
            onClick={() => handleChangePassword()}
            className="text-gray-2 bg-alpha-10 h-[48px] w-[170px] rounded-[8px] px-5 opacity-100 hover:opacity-60 text-[14px]"
          >
            {changepassLoading ? <ButtonLoading /> : 'Change password'}
          </button>
        </div>
      </div>
      {/* <div className="self-stretch flex flex-row items-start justify-start gap-[16px]">
        <div className="flex-1 flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            Two-factor authentication
          </div>
          <div className="self-stretch relative text-base leading-[24px] text-grey-4001 text-gray-1">
            Additional security measures (recommended)
          </div>
        </div>
        <div className="rounded-xl bg-blue1 w-11 h-6 shrink-0 overflow-hidden flex flex-row p-0.5 box-border items-center justify-end">
          <label className="relative inline-flex items-center cursor-pointer">
            <input type="checkbox" checked={twoFactor} onChange={(e) => handleChangeTwoFactor(e.target.checked)} className="sr-only peer" />
            <div className="w-11 h-6 bg-alpha-20 peer-focus:outline-none peer-focus:ring-4 peer-focus:ring-blue-300 dark:peer-focus:ring-blue-800 rounded-full peer dark:bg-gray-700 translate-x-15 peer-checked:after:left-[22px] peer-checked:after:border-grey-2 after:content-[''] after:absolute after:top-[2px] after:left-[2px] after:bg-grey-2 after:border-gray-2 after:bg-gray-2 after:border after:rounded-full after:h-5 after:w-5 after:transition-all dark:border-gray-2 peer-checked:bg-primary-blue"></div>
          </label>
        </div>
      </div> */}
      <div className="self-stretch relative box-border h-px shrink-0 border-t-[1px] border-solid border-alpha-20" />
      <div className="self-stretch flex flex-row items-start justify-start gap-[32px]">
        <div className="flex-1 flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            Delete account
          </div>
          <div className="self-stretch relative text-base leading-[24px] text-grey-4001 text-gray-1">
            Deleting your account will remove all your data and including your
            website forever.
          </div>
        </div>
        <div className="rounded-xl bg-light-101 h-12 flex flex-row py-4 box-border items-center justify-center text-center text-base text-red-5001">
          <button
            onClick={() => setOpen(true)}
            className="bg-alpha-10 h-[48px] rounded-[8px] px-5 text-red opacity-100 hover:opacity-60 text-[14px]"
          >
            Delete
          </button>
        </div>
      </div>
      <div
        className={
          'absolute flex top-0 left-0 w-[100vw] h-[100vh] bg-[#00000099] items-center justify-center z-20 backdrop-blur-[20px] ' +
          (open === true ? '' : 'hidden')
        }
      >
        <div className="flex flex-col bg-[#181A20] gap-[24px] p-[24px] rounded-[12px] w-[360px] z-20">
          <div className="text-gray-2 text-[18px] leading-[28px] font-medium">
            Are you sure you want to delete it?
          </div>
          <div className="flex flex-row justify-between gap-[12px]">
            <button
              onClick={() => handleDeleteAccount()}
              className="text-gray-2 bg-primary-blue h-[40px] rounded-[8px] px-5 w-full"
            >
              {loading ? <ButtonLoading /> : 'Confirm'}
            </button>
            <button
              onClick={() => setOpen(false)}
              className="text-gray-2 bg-alpha-10 h-[40px] rounded-[8px] px-5 w-full"
            >
              Cancel
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SecurityTab;
