import React from 'react';
import type { NextPage } from 'next';
import ProfileTab from './tabs/ProfileTab';
import Tab from '../../Tab';
import SecurityTab from './tabs/SecurityTab';
import { useState } from 'react';
import Profile from '../../Profile';

const Settings: NextPage = () => {
  const [tab, setTab] = useState(0);

  return (
    <div className="flex-1 h-[100vh] overflow-y-auto flex flex-col p-[24px] pb-[96px] md:p-[64px] box-border items-start justify-start gap-[30px] md:gap-[64px]">
      <div className="self-stretch flex flex-col md:flex-row items-start justify-start gap-[20px] md:gap-[64px]">
        <div className="flex flex-row items-center w-full md:w-fit md:order-last justify-start gap-[32px] text-lg">
          <Profile />
        </div>
        <div className="flex-1 flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative tracking-[-0.01em] leading-[30px] md:leading-[60px] text-[30px] md:text-[48px] font-bold text-gray-2">
            Account settings
          </div>
        </div>
      </div>

      <Tab selectedTab={tab} onChange={setTab} />

      <div className="max-w-[555px] flex flex-row items-start justify-start">
        {tab === 0 ? <ProfileTab /> : <SecurityTab />}
      </div>
    </div>
  );
};

export default Settings;
