import React, { useEffect } from 'react';
import type { ChangeEvent } from 'react';
import type { NextPage } from 'next';
import { ICON_NAMES, SVGIcon } from '@react-page/editor';
import { useState } from 'react';
import { toast } from 'react-toastify';
import { ButtonLoading } from '@react-page/editor';
import { ActionType, useStateContext } from '../../../../state';
import Cookies from 'js-cookie';
import { resizeBase64Image } from '../../../../utils/imageFunc';

interface MyElement extends HTMLElement {
  files: any;
}

const ProfileTab: NextPage = () => {
  const {
    state: { user, token },
    dispatch,
  } = useStateContext();
  const [firstname, setFirstname] = useState('');
  const [lastname, setLastname] = useState('');
  const [image, setImage] = useState('');

  useEffect(() => {
    setFirstname(Cookies.get('firstname') || '');
    setLastname(Cookies.get('lastname') || '');
    setImage(Cookies.get('image') || '');
  }, []);

  const [loading, setLoading] = useState(false);

  const handleSaveChange = async () => {
    setLoading(true);

    type Org = Record<string, string>;
    const data: Org = {};

    data.firstname = firstname;
    data.lastname = lastname;

    if (image !== Cookies.get('image')) {
      const resizedBase64 = await resizeBase64Image(image, 100, 100);
      data.image = resizedBase64;
    }

    const options = {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        authorization: 'bearer ' + Cookies.get('token'),
      },
      body: JSON.stringify(data),
    };

    try {
      await fetch(
        `${process.env.NEXT_PUBLIC_SERVER_API_URL}/settings/${Cookies.get(
          '_id'
        )}`,
        options
      )
        .then((res) => res.json())
        .then((res) => {
          if (res.code === 200) {
            setLoading(false);
            dispatch({
              type: ActionType.UPDATE_USER,
              payload: res.data,
            });
            toast.success('Successfully changed!');
          } else {
            toast.error(res.message);
          }
          setLoading(false);
        });
    } catch (error) {
      toast.error('Network Error!');
      setLoading(false);
    }
  };

  const handleOpen = () => {
    document.getElementById('fileInput')?.click();
  };

  const handleRemoveFile = () => {
    setImage(Cookies.get('image') || '');
    removeFileFromFileList(0);
  };

  const handleChange = (event: ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        const base64 = reader.result?.toString().split(',')[1];
        setImage(`data:image/png;base64,${base64}` || '');
      };
    }
  };

  function removeFileFromFileList(index: number) {
    const dt = new DataTransfer();
    const input = document.getElementById('fileInput') as MyElement;
    if (input) {
      const { files } = input;
      for (let i = 0; i < files.length; i++) {
        const file = files[i];
        if (index !== i) dt.items.add(file);
      }
      input.files = dt.files;
    }
  }

  return (
    <div className="flex-1 flex flex-col items-start justify-start gap-[40px] text-left text-xl text-primary-grey-501 font-text-xs-bold1">
      <b className="self-stretch relative text-[30px] leading-[38px] text-gray-2">
        Profile
      </b>
      <div className="self-stretch flex flex-col items-start justify-start gap-[16px]">
        <div className="self-stretch flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            Avatar
          </div>
          <div className="self-stretch relative text-base leading-[24px] text-gray-1">
            You can upload images up to 256x256. Your avatar shows up in your
            dashboard page. This is not public.
          </div>
        </div>
        <div className="flex flex-row items-center justify-start gap-[12px] text-center text-base text-grey-4001">
          <img
            className="relative w-20 h-20 shrink-0 object-cover rounded-full"
            alt=""
            src={image}
          />
          <div className="flex flex-row items-start justify-start gap-[8px]">
            <div className="rounded-xl h-12 flex flex-row py-4 px-5 box-border items-center justify-center">
              <button
                onClick={() => handleOpen()}
                className="text-gray-1 bg-alpha-10 h-[48px] rounded-[8px] px-5 opacity-100 hover:opacity-60 text-[14px]"
              >
                Change
              </button>
            </div>
            <button
              onClick={() => handleRemoveFile()}
              className="rounded-xl bg-alpha-10 w-12 h-12 shrink-0 flex flex-row box-border items-center justify-center opacity-100 hover:opacity-60"
            >
              <SVGIcon name={ICON_NAMES.REMOVE} />
            </button>
          </div>
          <input
            onChange={(e) => handleChange(e)}
            type="file"
            id="fileInput"
            style={{ display: 'none' }}
          ></input>
        </div>
      </div>
      <div className="self-stretch flex flex-row items-start justify-start gap-[16px]">
        <div className="self-stretch flex flex-col items-start justify-start gap-[16px] w-full">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            First Name
          </div>
          <div className="self-stretch flex flex-col items-start justify-start gap-[16px] text-lg">
            <input
              value={firstname}
              onChange={(e) => setFirstname(e.target.value)}
              className="bg-alpha-10 text-gray-2 text-sm rounded-lg border-[1px] focus:border-solid focus:outline-none border-white px-[20px] block w-full dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-alpha-20 h-[48px]"
              placeholder="..."
              required
            ></input>
          </div>
        </div>
        <div className="self-stretch flex flex-col items-start justify-start gap-[16px] w-full">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            Last Name
          </div>
          <div className="self-stretch flex flex-col items-start justify-start gap-[16px] text-lg">
            <input
              value={lastname}
              onChange={(e) => setLastname(e.target.value)}
              className="bg-alpha-10 text-gray-2 text-sm rounded-lg border-[1px] focus:border-solid focus:outline-none border-white px-[20px] block w-full dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-alpha-20 h-[48px]"
              placeholder="..."
              required
            ></input>
          </div>
        </div>
      </div>
      {/* <div className="self-stretch flex flex-col items-start justify-start gap-[16px]">
        <div className="self-stretch flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            Phone Number
          </div>
          <input
            onChange={(e) => setPhone(e.target.value)}
            value={phone}
            className="bg-alpha-10 text-gray-1 text-sm rounded-lg focus:outline-none focus:border-white px-[15px] block w-full p-2.5 dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-blue-500 h-[48px]"
            placeholder="..."
            required
          ></input>
        </div>
      </div> */}
      <div className="self-stretch flex flex-col items-start justify-start gap-[16px]">
        <div className="self-stretch flex flex-col items-start justify-start gap-[8px]">
          <div className="self-stretch relative leading-[28px] font-medium text-gray-2">
            Email address
          </div>
          <div className="self-stretch relative text-base leading-[24px] text-gray-1">
            This is used to log in
          </div>
          <input
            readOnly
            value={Cookies.get('email')}
            className="bg-alpha-10 text-gray-1 text-sm rounded-lg focus:outline-none focus:border-white px-[15px] block w-full p-2.5 dark:bg-gray-700 dark:placeholder-gray-400 dark:text-white dark:focus:ring-gray-2 dark:focus:border-blue-500 h-[48px]"
            placeholder="..."
            required
          ></input>
        </div>
      </div>
      <div className="self-stretch flex flex-row items-center justify-start">
        <div className="rounded-small bg-blue1 h-16 flex flex-row py-4 box-border items-center">
          <button
            onClick={() => handleSaveChange()}
            className="flex flex-row bg-primary-blue text-gray-3 h-[64px] px-[32px] w-[190px] items-center justify-center gap-[12px] rounded-[20px] opacity-100 hover:opacity-60 text-[18px]"
          >
            {loading ? <ButtonLoading /> : 'Save changes'}
          </button>
        </div>
      </div>
    </div>
  );
};

export default ProfileTab;
